import { Component } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { FlatpickrOptions } from 'ng2-flatpickr';
// For MDB Angular Pro
import { TimepickerModule } from 'ngx-bootstrap/timepicker';

@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.css']
})
export class AppComponent {
lightClock: any;
minDate = null;
cancelar = 'cancelar';

exampleOptions: FlatpickrOptions = {
    enableTime: true,
    noCalendar: true,
    dateFormat: 'H:i',
};
// maxDate = new Date(2018, 7, 20);

  readonly group2: FormGroup;
  title = 'datepickpoc';
  date: Date = new Date();
  settings = {
    bigBanner: true,
    timePicker: true,
    format: 'dd/MM/yyyy',
    defaultOpen: false
  };

  constructor(private readonly _formBuilder: FormBuilder) {
    this.group2 = this._formBuilder.group({
      dateMinMax: [null, Validators.required],
      maxDate: null,
      minDate: null,
      dateflat: null
    });
  }
  disabledChange(disabled: boolean) {
    this.minDate = new Date(2018, 7, 15);
  }
}
